import React from "react";

export default class AddPlayer extends React.Component {
  fileInput = React.createRef();
  state = { playerName: "" };
  handleSubmit = async event => {
    const playerRef = await playersCollection.doc();

    const fileRef = firebase
      .storage()
      .ref()
      .child(playerRef.id);

    const uploadProgress = fileRef.put(file);

    uploadProgress.on(
      "state_changed",
      snapshot => {
        const progress =
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        addPlayerBtn.setAttribute("disabled", true);
        addPlayerBtn.innerText = "Uploading...";
        console.log(`Upload Progress: ${progress}%`);
      },
      error => console.error(error),
      () => {
        playerRef.set({ playerName });
        playerNameInput.value = "";
        uploadInput.value = "";
        addPlayerBtn.removeAttribute("disabled");
        addPlayerBtn.innerText = "Add Player";
      }
    );
  };
  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <h2>Add a player by uploading his name and image here.</h2>
        <p>
          The name will be used when you want to bring that player onto the
          screen
        </p>

        <input
          type="text"
          id="playerNameInput"
          name="name"
          required
          placeholder="Player Name"
          value={this.state.playerName}
          onChange={({ target }) =>
            this.setState(state => ({
              ...state,
              playerName: target.value
            }))
          }
        />
        <input
          id="uploadInput"
          type="file"
          name="photo"
          required
          ref={this.fileInput}
          accept="image/png, image/jpeg, image/jpg"
        />
        <br />
        <br />
        <button id="addPlayerBtn">Add Player</button>
      </form>
    );
  }
}



